@use 'src/styles/reset.scss';body{margin:0;font-family:'Ramaraja', serif;background-color:#F2CFDC;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}

*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}.submit{font-family:'Ramaraja', serif;font-size:1rem;background-color:#E2A9BD;border:none;border-radius:30px;padding:1rem;margin:1.5rem;padding-left:2rem;padding-right:2rem;cursor:pointer}.submit:hover{box-shadow:inset 4px 4px 4px rgba(0,0,0,0.25)}.link>*{color:#E2A9BD;font-family:'Ramaraja', serif;font-size:1rem}

.title{text-align:center;padding:1rem}.logo{margin:auto;width:40%}

@use 'src/styles/reset.scss';.input{font-family:'Ramaraja', serif;border-radius:30px;background-color:#E9DBDB;box-shadow:inset 4px 4px 4px rgba(0,0,0,0.25);border:none;padding:1rem;margin:1rem;text-align:center}.input::-webkit-input-placeholder{color:#ADABAB;text-align:center}.input:-ms-input-placeholder{color:#ADABAB;text-align:center}.input::placeholder{color:#ADABAB;text-align:center}.input:focus{outline:none;text-align:left}

*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}.submit{font-family:'Ramaraja', serif;font-size:1rem;background-color:#ACC5A6;border:none;border-radius:30px;padding:1rem;margin:1.5rem;padding-left:2rem;padding-right:2rem;cursor:pointer}.submit:hover{box-shadow:inset 4px 4px 4px rgba(0,0,0,0.25)}.link>*{color:#000000;font-family:'Ramaraja', serif;font-size:1rem}@keyframes movedown{100%{transform:translateY(100%);top:100vh}}.svgs{display:flex;flex-flow:row wrap;justify-content:center;align-items:center;height:100vh;width:100%;margin:auto;position:absolute;z-index:-3}.svg-text{width:50%;display:block;margin:auto;transform:translateY(0)}.intro{width:60%;padding:2rem}

@use 'src/styles/reset.scss';*{font-family:'Ramaraja', serif}*:focus{outline:none}.breebe-form{margin:auto}.breebe-form--input{width:90%}.breebe-form--submit{background:linear-gradient(90deg, #A4CDBD -35.29%, #D79A9A 100%);border:1px solid #CFB0BB;box-sizing:content-box;box-shadow:0px 4px 4px rgba(0,0,0,0.25);border-radius:30px;padding-left:.5rem;padding-right:.5rem;padding-top:0;margin-bottom:.5rem;width:10%}.get-breebes,.breebe-form--submit,.tags--tag,.user--logout,.single-breebe--edit,.single-breebe--back,.single-breebe--delete{cursor:pointer}.get-breebes{box-shadow:4px 4px 4px rgba(0,0,0,0.25);border-radius:30px;padding:1rem;background-color:#E2A9BD;border:none}.breebes{display:flex;flex-flow:row wrap;width:90%;margin:auto}.single-breebe{width:100%;display:flex;font-family:'Jacques Francois', serif;align-items:center;justify-content:center;box-sizing:content-box;border-radius:30px;background-color:none;padding:.5rem;margin:.5rem}.single-breebe--form{display:flex;align-items:center;justify-content:center;box-sizing:content-box}.single-breebe--input{display:block;border:none;border-radius:30px;margin:auto;background-color:#E9DBDB;width:90%;text-align:center;box-sizing:content-box}.single-breebe--tag-edit{display:block;border:none;border-radius:30px;margin:auto;background-color:#E9DBDB;text-align:center;box-sizing:content-box}.single-breebe--text{display:inline-block;width:80%}.single-breebe--icons{display:inline-block;width:20%}.single-breebe--edit{display:inline-block;width:30%}.single-breebe--delete{display:inline-block;width:30%}.single-breebe--edit-pen{width:10%}.single-breebe--back{width:10%}.svg-text-sub{width:40%;display:block;margin:auto;transition:all .2s ease-in-out}.modal{position:fixed;top:0;left:0;width:100%;height:100%;background:rgba(0,0,0,0.6)}.modal--label{color:#E2A9BD}.modal--title{margin:.5rem;font-size:.8rem}.modal-main{position:fixed;background:#FFD8D8;width:80%;border-radius:30px;height:auto;top:50%;left:50%;transform:translate(-50%, -50%)}.loader{position:absolute;top:30vh;margin-left:auto;margin-right:auto;width:20%;z-index:10}@keyframes rotation{from{transform:rotate(0deg)}to{transform:rotate(359deg)}}.display--loader{position:fixed;top:50%;left:50%;z-index:10;width:10%;margin:auto;animation:rotation 5s infinite linear}.display--none{display:none}.cloud{width:80%;height:90%;margin:auto}.error{padding-bottom:.5rem}

.user--name{font-family:'Beth Ellen', cursive;font-size:.8em;position:absolute;top:0;left:0%;background-color:#E2A9BD;border-radius:30px;padding:1rem;margin:1rem;cursor:pointer}.user--logout{font-family:'Beth Ellen', cursive;font-size:.8em;position:absolute;top:0;right:0%;border-radius:30px;padding:1rem;background-color:#ACC5A6;margin:1rem;border:none}.user--intro{font-family:'Jacques Francois', serif;line-height:2rem}.bolder{font-weight:700}

@use 'src/styles/reset.scss';.tags{display:flex;flex-flow:row wrap;width:90%;margin:auto;padding:1rem}.tags--tag,.tags .all{font-family:'Ramaraja', serif;width:10%;background-color:#FFD8D8;box-shadow:4px 4px 4px rgba(0,0,0,0.25);border-radius:30px;padding:.5rem;margin-right:.5rem;box-sizing:content-box}


